</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <store-group @StoreGroupChange="StoreGroupChange"></store-group>
        
        <el-form-item label="所属团长店">
          <el-select class="search-input" v-model="searchContent.storeLeaderId" filterable placeholder="请选择所属团长店" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in storeLeaderOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="业务类型">
          <el-select class="search-input" v-model="searchContent.businessType" filterable placeholder="请选择业务类型"
            clearable size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in businessTypeOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="配送类型">
          <el-select class="search-input" v-model="searchContent.orderType" filterable placeholder="请选择配送类型" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option v-for="item in orderTypeOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="订单号">
          <el-input v-model.trim="searchContent.orderId" size="small" class="search-input" placeholder="请输入订单号"
            @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>

        <el-form-item label="收货人手机号">
          <el-input v-model.trim="searchContent.receiverPhone" size="small" class="search-input" placeholder="收货人手机号"
            @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>

        <el-form-item label="下单时间">
          <date-picker-range 
            :defaultRange="searchContent.defaultRange"
            @datePickerRangeChange="datePickerRangeChange">
          </date-picker-range>
        </el-form-item>

        <el-form-item label="订单状态">
          <el-select class="search-input" v-model="searchContent.orderStatus" placeholder="请选择订单状态" clearable
            size="small" @change="doSearchHandel(true)">
            <el-option :label="item.value" :value="item.code" :key="item.code" v-for="item in orderStatusOptions">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table ref="multipleTable" class="fixed-height" v-loading="loading" :data="tableData" empty-text="没有查询到您想要的数据"
        @select="handleSelect" @select-all="handleSelectAll" style="width:100%;" size="small">
        <el-table-column min-width="80px" prop="platformStr" label="订单来源"></el-table-column>
        <el-table-column min-width="160px" prop="orderId" label="订单号">
          <template slot-scope="scope" size="small">
            <div class="order-id">
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.orderId}}</router-link>
            </div>
          </template>
        </el-table-column>
        <!-- <el-table-column min-width="80px" prop="orderType" label="订单类型" :formatter="orderTypeFormatter"> -->
        <el-table-column min-width="100px" prop="businessTypeStr" label="业务类型" :formatter="emptyFormatter">
        </el-table-column>
        <el-table-column min-width="86px" prop="orderTypeStr" label="配送类型" :formatter="emptyFormatter">
        </el-table-column>
        <el-table-column min-width="110px" prop="storeName" label="所属门店"></el-table-column>
        <el-table-column min-width="130px" label="所属团长店">
          <template slot-scope="scope" size="small">
            <p v-if="scope.row.storeLeaderId">{{scope.row.storeLeaderId}}_{{scope.row.virtualStoreName}}_{{scope.row.userName}}</p>
            <p v-else>-</p>
          </template>
        </el-table-column>
        <el-table-column min-width="120px" prop="buyerName" label="收货人"></el-table-column>
        <el-table-column min-width="120px" prop="buyerPhone" label="收货手机号"></el-table-column>
        <el-table-column min-width="90px" prop="orderPayAmount" label="订单实付"></el-table-column>
        <el-table-column min-width="80px" prop="orderStatusStr" label="订单状态" :formatter="nullTreating">
        </el-table-column>
        <el-table-column min-width="170px" prop="createTime" label="下单时间" :formatter="nullTreating"></el-table-column>
        <el-table-column min-width="170px" prop="prePickUpTimeFrom" label="开始自提时间"></el-table-column>
        <el-table-column fixed="right" label="操作" width="140">
          <template slot-scope="scope" size="small">
            <div>
              <el-button type="text" size="mini" @click="showChangePickUpStartDateTime(scope.row)" icon="el-icon-date">修改履约时间</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div style="padding: 10px;">
        <p><span style="color:red; padding-right:10px;">*</span>查询到订单信息，核对无误后，可操作订单修改自提门店；整单退款原路退回；已自提未核销的订单后台核销；</p>
        <p><span style="color:red; padding-right:10px;">*</span>批量置为过期订单，仅用于超时未提截单后置为过期，且不给用户退款，谨慎操作。</p>
      </div>
      <!-- 分页条 -->
      <el-pagination v-show="tableData.length>0" class="tc mt20" background @size-change="changeSize"
        @current-change="changeCurrent" :current-page.sync="pageNum" :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <el-dialog title="修改履约时间" :visible.sync="changePickUpStartDateTime" width='480px'>
      <el-form ref="form" label-width="110px">
        <el-form-item label="履约时间" required>
          <el-date-picker
            class="search-date-picker"
            size="small"
            v-model="pickUpStartDateTime"
            type="datetime"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder="履约时间">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="changePickUpStartDateTime = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="pickUpStartDateTimeChangeHandel" size="mini" :disabled="isDisabled">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import config from '@/utils/config'
  import * as utils from '@/utils/utils'
  import DatePickerRange from '@/components/Common/DatePickerRange'
  import StoreGroupAndStore from '@/components/Common/StoreCommon/StoreGroupAndStore'
  const orderMgtUrl = {
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    // 订单查询接口
    orderQuery: '/plateform/a/order/orderQuery',
    // 获取订单状态
    getOrderStatus: '/plateform/a/order/getOrderStatus',
    // 处理单据
    dealOrder: '/member/order/deal/dealOrder',
    // 获取单据列表
    getOrderListPage: '/member/order/deal/getOrderListPage',
    // 获取配送公司列表
    getMobileLogisticsList: '/plateform/a/baseCommon/store/getMobileLogisticsList',
    // 订单修改配送信息
    updateOrderLogistics: '/plateform/a/logistics/updateOrderLogistics',
    // 配送类型为快递配送并且订单状态为配送中的时候的退款
    completeAndRefund: '/member/order/deal/completeAndRefund',
    // 获取退款信息
    toRefund: '/member/a/mobile/refund/toRefund',
    // 获取退款原因
    getReason: '/member/a/mobile/refund/getReason',
    // 退款处理
    doRefund: '/member/a/mobile/refund/doRefund',
    // 获取所属团长店信息
    getStoreLeaderList: '/foundation/p/apply/leader/queryList'
  }
  const salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
  const numReg = /^[1-9]*[1-9][0-9]*$/
  export default {
    data() {
      return {
        pageSizes: config.pageSizes20,
        userName: Storage.get("loginInfo") ? Storage.get("loginInfo").userName : '',
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchContent: {
          storeLeaderId: '', // 所属团长店
          storeGroupTypeNo: '',
          storeGroupNo: '',
          platformId: 5, // 订单来源
          storeNo: '', // 门店编码
          orderId: '', // 订单号
          businessType: '', // 业务类型
          orderType: '', // 配送类型
          orderStatus: '',
          defaultRange: [],
          startDate: '', // 下单开始时间
          endDate: '', // 下单结束时间
          receiverPhone: '' // 收货人手机号
        },
        businessTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '预售',
            value: 3
          },
          // {
          //   label: '短周期预售',
          //   value: 2
          // },
          {
            label: '及时达',
            value: 4
          },
          {
            label: '拼团',
            value: 5
          },
          {
            label: '虚拟订单',
            value: 6
          }
        ],
        orderTypeOptions: [{
            label: '全部',
            value: ''
          },
          {
            label: '及时送',
            value: 1
          },
          {
            label: '预约送',
            value: 2
          },
          {
            label: '自提',
            value: 3
          }
        ],
        // 订单状态下拉框
        orderStatusOptions: [{
            code: '',
            value: '全部'
          },
          {
            code: 0,
            value: '支付成功'
          },
          {
            code: 3,
            value: '待配送'
          },
          {
            code: 4,
            value: '待自提'
          },
          {
            code: 5,
            value: '配送中'
          },
          {
            code: 6,
            value: '已完成'
          },
          {
            code: 7,
            value: '已取消'
          },
          {
            code: 8,
            value: '待支付'
          },
          {
            code: 10,
            value: '已退款'
          },
          {
            code: 11,
            value: '已失效'
          },
          {
            code: 100,
            value: '待成团'
          },
          {
            code: 101,
            value: '成团失败'
          }
        ],
        // 门店列表下拉框
        storeOptions: [],
        tableData: [],
        // 多选选中的内容
        checkedList: [],

        isDisabled: false, // 是否禁用
        dialogChangeStoreVisible: false, // 修改门店弹窗
        // 订单改门店时的门店编号
        storeNo: '',
        // 修改门店的参数ID
        changeStoreOrderIds: [],

        // 修改配送公司弹窗展示
        dialogChangeOrderVisible: false,
        logisticsCompanyOptList: [],
        orderNo: '', // 订单号
        logisticsCompanyId: '', // 配送公司code 1001 门店自配送    1002 美团配送
        logisticsId: '', // 该订单，该配送公司所对应的id

        // 部分退款
        refundDialogVisible: false,
        refundPartLoading: false,
        currentRowMsg: {},
        // 退款原因
        refundReasonList: [],
        // 退款商品信息
        orderBaseMsg: {},
        refundVoList: [], // 退款商品信息
        reasonCode: '', // 退款原因编码
        skuNum: 0,
        totalAmount: 0,
        totalNum: 0,

        storeLeaderOptions: [],
        changePickUpStartDateTime: false,
        pickUpStartDateTime: '',
        changeTimeOrderId: '',
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7
          }
        }

      }
    },
    created() {
      // 根据用户选择的城市获取门店
      this.getStoreList()
      // 获取所属团长店信息
      this.getStoreLeaderList()
    },
    beforeMount() {
      let searchContentMesg = Storage.get("searchContentForOpe")
      if (this.$route.meta.keepAlive && searchContentMesg) {
        this.searchContent = searchContentMesg.searchContent

        Storage.remove('searchContentForOpe')
      }
    },
    mounted() {
      // 根据用户选择的城市获取数据
      this.doSearchHandel(true)
    },
    components: {
      "date-picker-range": DatePickerRange,
      "store-group": StoreGroupAndStore
    },
    methods: {
      StoreGroupChange(params) {
        this.searchContent.storeNo = params.storeNo
        this.searchContent.storeGroupTypeNo = params.storeGroupTypeNoList[0]
        this.searchContent.storeGroupNo = params.storeGroupTypeNoList[1]
        this.doSearchHandel(true)
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
       getStoreList(typeNo, groupNo) {
        this.$http.post(orderMgtUrl.search, {storeGroupTypeNo: typeNo || null, storeGroupNo: groupNo || null}).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.length ? res.data : []
            this.storeOptions = optionsArr.map(item => {
              return {
                value: item.storeNo,
                label: item.storeNo + '-' + item.storeName
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize(value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent(value) {
        this.currentPage = value
        this.doSearchHandel()
      },
      /**
       * [getOrderStatus 获取订单状态]
       * @return {[type]} [description]
       */
      getOrderStatus() {
        this.$http.post(orderMgtUrl.getOrderStatus).then(res => {
          if (res.code === 0) {
            if (res.data && res.data.length) {
              this.orderStatusOptions = res.data
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取订单状态):", e.message)
        })
      },
      /**
       * [doSearchHandel 获取订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        this.checkedList = []
        this.loading = true
        if (_param === true) {
          this.pageNum = 1
        }
        this.$http.post(orderMgtUrl.getOrderListPage, {
          ...this.searchContent,
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }).then(res => {
          this.loading = false
          if (res.code === 0) {
            if (res.data && res.data.result && res.data.result.length) {
              for (let item of res.data.result) {
                item.redirectUrl = `/order_mgt/detail/${item.orderId}`
              }
              this.tableData = res.data.result
              if (this.pageNum == 1) {
                this.total = res.data.total
              }
            } else {
              this.tableData = []
              this.total = 0
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if (utils.isKeyInArrayBykey(row.orderId, 'orderId', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('orderId', row.orderId, this.checkedList)
          return
        } else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.orderId] ? '' : hash[next.orderId] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if (val.length > 0) {
          // 全选
          for (let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.orderId] ? '' : hash[next.orderId] = true && item.push(next)
            return item
          }, [])
        } else {
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for (let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'orderId')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : []
        }
      },
      /**
       * 数据处理
       */
      nullTreating(row, column, cellValue) {
        if (!cellValue) {
          return '--'
        } else {
          return cellValue
        }
      },
      /**
       * [orderTypeFormatter 格式化订单类型]
       */
      orderTypeFormatter(row, column, cellValue) {
        if (cellValue === 1) {
          return '及时送'
        } else if (cellValue === 2) {
          return '预约送'
        } else if (cellValue === 3) {
          return '自提单'
        }
      },
      emptyFormatter(row, column, cellValue) {
        return cellValue && cellValue != "" ? cellValue : '--'
      },
      /**
       * @description: 获取所属团长店信息
       * @param {type} 
       * @return: 
       */
      getStoreLeaderList() {
        this.$http.post(orderMgtUrl.getStoreLeaderList, {pageNum: 1, pageSize: -1}).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.storeLeaderOptions = optionsArr.filter(item => {
              return item.applyStatus === 2 && item.userStatus === 0
            }).map(item => {
              return {
                id: item.id,
                applyStatus: item.applyStatus,
                virtualStoreName: item.virtualStoreName,
                userName: item.userName,
                actualStoreNo: item.actualStoreNo,
                value: item.id,
                label: `${item.id}_${item.virtualStoreName}_${item.userName}`
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取所属团长店信息):", e.message)
        })
      },
      /** 
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.startDate = result ? result[0] : ''
        this.searchContent.endDate = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      showChangePickUpStartDateTime(_row) {
        this.changePickUpStartDateTime = true
        this.changeTimeOrderId = _row.orderId
        this.pickUpStartDateTime = ''
      },
      pickUpStartDateTimeChangeHandel(_row) {
        let _params = {
          opType: 6, 
          orderIds: [this.changeTimeOrderId],
          pickUpStartDateTime: this.pickUpStartDateTime
        }
        utils.showLoading()
        this.isDisabled = true
        this.$http.post(orderMgtUrl.dealOrder, _params).then(res => {
          // 清空loading
          utils.clearLoading()
          this.isDisabled = false
          if (res.code === 0) {
            this.$message.success("修改履约时间成功")
            this.doSearchHandel()
            this.changePickUpStartDateTime = false
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          // 清空loading
          utils.clearLoading()
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave(to, from, next) {
      from.meta.keepAlive = false;
      next();
    }

  }

</script>



